"use client"
import { motion } from 'framer-motion'

export default function PageHero({ title, description }: { title: string; description?: string }) {
  return (
    <section className="page-hero">
      <div className="page-hero-inner">
        <motion.h1 initial={{ opacity: 0, y: 8 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.6 }} className="page-hero-title">
          {title}
        </motion.h1>
        {description && <p className="page-hero-desc">{description}</p>}
      </div>
    </section>
  )
}
